<template>
  <div class="container_box">
    <div class="search_wapper">
      <!-- 标题 -->
      <div class="search_box_logo">面位搜索</div>
      <div class="search_box_context">
        <!-- 搜索框 -->
        <div class="input_wapper" @click="selectBoxAnimationEvent">
          <input
            id="search_keyword"
            autocomplete="off"
            placeholder="你好世界 | Hello World | Привет мир | Bonjour le monde | こんにちは世界 | Ciao mondo | Hallo Welt | 안녕 세상 | Saluton mondo."
            v-model="visualKeyword"
            @input="keywordInput"
            v-on:keyup.up="keywordOperate('up')"
            v-on:keyup.down="keywordOperate('down')"
            v-on:keyup.enter="keywordOperate('enter')"
          />
        </div>
        <!-- 搜索提示框 -->
        <SearchTips :keyword="keyword" :keywordCallback="setkeyword" ref="refSearchTips" />
      </div>
      <!-- 搜索按钮组 -->
      <SearchButton :keyword="keyword" />
    </div>
  </div>
</template>

<script>
import SearchButton from "./SearchButton";
import SearchTips from "./SearchTips";

const vmDataModel = {
  keyword: "",
  tipsDisplay: true,
  visualKeyword: ""
};

export default {
  name: "SearchBox",
  props: {},
  components: {
    SearchButton,
    SearchTips
  },
  data() {
    return vmDataModel;
  },
  methods: {
    setkeyword(text) {
      this.keyword = this.visualKeyword = text;
    },
    keywordInput() {
      this.keyword = this.visualKeyword;
      this.$refs.refSearchTips.keywordInputEvent(this.keyword);
    },
    keywordOperate(operate) {
      this.$refs.refSearchTips.tipsListOperate(operate);
    },
    selectBoxAnimationEvent() {}
  }
};
</script>

<style scoped>
.search_box_context {
  text-align: center;
}
/* 搜索输入栏 */
#search_keyword {
  border: none;
  margin: 0 0px;
  height: 100%;
  width: 100%;
  min-height: 40px;
  padding: 0 8px;
  transition: all 0.5s;
}
/* #search_keyword:hover {
  margin: 0 4px;
} */

/* 搜索框装饰器 */
.input_wapper {
  overflow: hidden;
  border-radius: 2px;
  box-shadow: 0 0 4px #555353;
  background-color: #fff;
  transition: all 1s;
}
.input_wapper:hover {
  margin: 0 4px;
  box-shadow: 0 0 8px #4f73d8;
  border-radius: 1px;
}

/* logo 文字 */
.search_box_logo {
  color: #262626;
  display: block;
  font-size: 18px;
  font-weight: 700;
  text-align: left;
  margin-bottom: 14px;
}
.search_wapper {
  width: 100%;
}
</style>
